@font-face {
    font-family: 'code';
    src: url('./fonts/ProggySquare.ttf') format('truetype'); 
    font-weight: normal;
    font-style: normal;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
    margin: 0;
    font-family: 'code', Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#preloader {
    background: #000; 
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}

#ascii-frog {
    color: #32CD32; 
    font-family: 'code'; 
    font-size: clamp(1.25rem, 2.5vw, 2rem); 
    text-align: center;
    margin: auto; 
    transform: translate(-50%, -50%);
}

header {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    padding: 1rem;
}

.logo {
    font-family: 'code', Arial, Helvetica, sans-serif;
    text-align: center;
    font-size: clamp(1rem, 2vw, 1rem);
    color: black;
}

.aboutme {
    color: black;
    font-size: clamp(0.875rem, 1.5vw, 1.125rem); 
    font-family: 'code', Arial, Helvetica, sans-serif;
    text-align: center;
    padding: 0.5rem;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.5;
    max-width: 90vw;
}

.social-links {
    display: flex;
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
}

.email-link {
    font-family: 'code';
    font-size: clamp(1rem, 2vw, 1.5rem);
    text-decoration: none;
    color: black;
    font-weight: bold;
    transition: color 0.3s ease;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.email-link:hover {
    color: #32CD32;
}

form {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 2rem;
    width: 80%;
    max-width: 400px;
}

/* Tablet view */
@media (max-width: 768px) {
    .aboutme {
        font-size: 0.875rem;
        line-height: 1.2;
        width: 90%;
    }
}

/* Mobile view */
@media (max-width: 600px) {
    .social-links {
        flex-direction: column; 
        gap: 0.5rem;
        width: 100%;
        left: 0;
        transform: none;
        padding: 0 1rem;
        align-items: center;
    }

    .email-link {
        font-size: 1rem; 
    }

    form {
        top: 85%;
        padding: 1.5rem;
    }
}
